@keyframes spin {
    from {
        transform: rotateZ(0);
    }

    to {
        transform: rotateZ(360deg)
    }
}

.loading-spinner {
    width: 50px;
    height: 50px;
    animation: spin 2s infinite linear ;
    transform-origin: center;
    margin:1rem 9rem;
    overflow: hidden;
}